  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Asul', sans-serif;
}

a {
  min-height: 44px;
  min-width: 44px;
  display: inline-block;
}

body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  font-size: 12px;
  color: #FDFEFE;
  background-color: #222831;
}

/* Skip to content */
.skip-link {
  position: absolute;
  top: -44px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
}
 
.skip-link:focus {
   top: 0;
}

/* Header */
header {
  min-height: 56px;
  transition: min-height 0.3s;
  background-color: #393E46;
}

.header__inner {
  text-align: center;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.header__inner a {
  margin-bottom: 7px;
}

.header__title {
  font-weight: 300;
  font-size: 3em;
  padding: 0.75em;
  margin-left: 0.5em;
  display: inline-block;
  color: white;
}

.nav {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.nav__list {
  width: 100%;
  padding: 0;
  margin: 0;
}

.nav__item {
  box-sizing: border-box;
  display: inline-block;
  width: 24%;
  line-height: 24px;
  text-transform: uppercase;
}

.nav a {
  display: inline-block;
  padding: 1em;
  text-decoration: none;
  color: white;
  font-size: 1.5em;
}

.nav a:hover {
  text-decoration: solid;
  color: #a4a8b1;
}

.header__menu {
  font-size: 32px;
  margin: auto;
  display: block;
  width: 30px;
  color: white;
}

/* Main */
.scrollUp {
  display: inline;
  z-index: 999;
  padding: 10px 12px;
  text-align: center;
  background: #00ADB5;
  color: white;
  border-radius: 50%;
  font-weight: 700;
  text-decoration: none;
  position: fixed;
  bottom: 80px;
  right: 15px;
  font-size: 20px;
}

/* Jumbotron */
.hero {
  display: flex;
  align-items: center;
  min-height: 600px;
  width: 100%;
  text-align: center;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
  background-color: #2E86C1 ;
  background-size: cover;
}

.hero__inner {
  margin: 0 auto;
  max-width: 800px;
  opacity: 0.8;
}

.hero__title {
  color: #2E86C1;
  font-weight: 500;
  font-size: 56px;
}

.hero__tagline {
  color: #2E86C1;
  margin-top: 16px;
  font-size: 28px;
  font-weight: 300;
}

main {
  /* width: 100%; */
  margin: 0 auto;
  background-color: #2E86C1;
}

.content {
  padding: 32px;
}

.restaurant {
  width: 100%;
  margin: 60px auto;
  text-align: center;
}

.restaurant__label {
  font-size: 32px;
  font-weight: lighter;
  margin-top: 50px;
}

.restaurant__label::after {
  content: '';
  margin-top: 16px;
  display: block;
  border-bottom: 1px solid #eeeeee;
}

/* Post */
.posts {
  margin: 32px auto auto;
  text-align: left;
}

/* Post Item */
.post-item {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
}

.post-item__content {
  padding: 16px;
}

.post-item__thumbnail {
  width: 100%;
  margin-bottom: 10px;
}

.post-item__rate {
  font-size: 15px;
  text-transform: uppercase;
}

.post-item__location {
  font-size: 12px; 
  padding: 10px 0;
  border-radius: 8px;
}

.post-item__location a {
  text-decoration: none;
  color: #00ADB5;
}

.post-item__title {
  font-weight: 500;
  font-size: 20px;
  margin-top: 16px;
  transition: 0.3s opacity;
}

.post-item__title:hover {
  opacity: 0.5;
}

.post-item__title a {
  text-decoration: none;
  color: inherit;
  
}

.post-item__description {
  font-size: 12px;
  line-height: 1.5em;
}

.post-item .post-item__content .post-item__description {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 8; /* number of lines to show */
  -webkit-box-orient: vertical;
}

/* Quotes */
.quotes{
  font-size: 1.4em;
  width: 60%;
  margin: 50px auto;
  font-family: Open Sans;
  color: #FDFEFE;
  padding: 1.5em;
  border-left: 8px solid #00ADB5;
  line-height: 1.6;
  font-style: italic;
}

.quotes span{
  display: block;
  color:#FDFEFE;
  font-style: normal;
  font-weight: bold;
  margin-top: 1em;
}

/* Footer */
footer {
  background-color: #2E86C1;
  text-align: center;
  height: 40px;
  width: 100%;
}

footer p {
  color: white;
  padding: 10px 0;
  font-size: 15px;
}

/* Detail */
.detail {
  margin: 0 auto;
  padding: 20px;
  width: 100%;
  max-width: 800px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 18px 16px;
}

.detail .restaurant__poster {
  width: 100%;
  max-width: 400px;
}

.detail .restaurant__info h4 {
  margin: 8px 0;
}

.detail .restaurant__review {
  border: 3px solid #00ADB5;
  border-radius: 8px;
  padding: 10px;
  margin-bottom: 40px;
}

.detail .restaurant__title {
  margin-top: 40px;
}

/*
  Like
*/

.like {
  font-size: 18px;
  position: fixed;
  bottom: 16px;
  right: 16px;
  background-color: #00ADB5;
  color: white;
  border: 0;
  border-radius: 50%;
  width: 55px;
  height: 55px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.liked {
  font-size: 18px;
  position: fixed;
  bottom: 16px;
  right: 16px;
  background-color: white;
  color: #00ADB5;
  border: 1px solid #00ADB5;
  border-radius: 50%;
  width: 55px;
  height: 55px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Restaurants */
.restaurants {
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
  margin: 32px 0;
}

.restaurant__menu {
  display: grid;
}

.restaurant__menu h4 {
  margin-bottom: 6px;
}

.restaurant__description h3 {
  margin-bottom: 6px;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AACA;EACE,UAAU;EACV,SAAS;EACT,sBAAsB;EACtB,+BAA+B;AACjC;;AAEA;EACE,gBAAgB;EAChB,eAAe;EACf,qBAAqB;AACvB;;AAEA;EACE,SAAS;EACT,UAAU;EACV,WAAW;EACX,YAAY;AACd;;AAEA;EACE,eAAe;EACf,cAAc;EACd,yBAAyB;AAC3B;;AAEA,oBAAoB;AACpB;EACE,kBAAkB;EAClB,UAAU;EACV,OAAO;EACP,yBAAyB;EACzB,YAAY;EACZ,YAAY;EACZ,YAAY;AACd;;AAEA;GACG,MAAM;AACT;;AAEA,WAAW;AACX;EACE,gBAAgB;EAChB,2BAA2B;EAC3B,yBAAyB;AAC3B;;AAEA;EACE,kBAAkB;EAClB,WAAW;EACX,iBAAiB;EACjB,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;EAChB,cAAc;EACd,eAAe;EACf,kBAAkB;EAClB,qBAAqB;EACrB,YAAY;AACd;;AAEA;EACE,WAAW;EACX,iBAAiB;EACjB,kBAAkB;EAClB,kBAAkB;AACpB;;AAEA;EACE,WAAW;EACX,UAAU;EACV,SAAS;AACX;;AAEA;EACE,sBAAsB;EACtB,qBAAqB;EACrB,UAAU;EACV,iBAAiB;EACjB,yBAAyB;AAC3B;;AAEA;EACE,qBAAqB;EACrB,YAAY;EACZ,qBAAqB;EACrB,YAAY;EACZ,gBAAgB;AAClB;;AAEA;EACE,sBAAsB;EACtB,cAAc;AAChB;;AAEA;EACE,eAAe;EACf,YAAY;EACZ,cAAc;EACd,WAAW;EACX,YAAY;AACd;;AAEA,SAAS;AACT;EACE,eAAe;EACf,YAAY;EACZ,kBAAkB;EAClB,kBAAkB;EAClB,mBAAmB;EACnB,YAAY;EACZ,kBAAkB;EAClB,gBAAgB;EAChB,qBAAqB;EACrB,eAAe;EACf,YAAY;EACZ,WAAW;EACX,eAAe;AACjB;;AAEA,cAAc;AACd;EACE,aAAa;EACb,mBAAmB;EACnB,iBAAiB;EACjB,WAAW;EACX,kBAAkB;EAClB,yDAAgE;EAChE,2BAA2B;EAC3B,0BAA0B;EAC1B,sBAAsB;AACxB;;AAEA;EACE,cAAc;EACd,gBAAgB;EAChB,YAAY;AACd;;AAEA;EACE,cAAc;EACd,gBAAgB;EAChB,eAAe;AACjB;;AAEA;EACE,cAAc;EACd,gBAAgB;EAChB,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,iBAAiB;EACjB,cAAc;EACd,yBAAyB;AAC3B;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,WAAW;EACX,iBAAiB;EACjB,kBAAkB;AACpB;;AAEA;EACE,eAAe;EACf,oBAAoB;EACpB,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,gBAAgB;EAChB,cAAc;EACd,gCAAgC;AAClC;;AAEA,SAAS;AACT;EACE,sBAAsB;EACtB,gBAAgB;AAClB;;AAEA,cAAc;AACd;EACE,0CAA0C;EAC1C,WAAW;EACX,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,WAAW;EACX,mBAAmB;AACrB;;AAEA;EACE,eAAe;EACf,yBAAyB;AAC3B;;AAEA;EACE,eAAe;EACf,eAAe;EACf,kBAAkB;AACpB;;AAEA;EACE,qBAAqB;EACrB,cAAc;AAChB;;AAEA;EACE,gBAAgB;EAChB,eAAe;EACf,gBAAgB;EAChB,wBAAwB;AAC1B;;AAEA;EACE,YAAY;